import React from 'react';
import Header from "./components/Header";
import Jumbotron from "./components/Jumbotron";
import ImageCard from "./components/ImageCard";
import Footer from "./components/Footer";
import lists from "./clicky.json";
import "./App.css";

function shuffleCards(array) {
  for (let i = array.length - 1; i > 0; i--) {
    let j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
};

class App extends React.Component {
  state = {
    lists: lists,
    currentScore: 0,
    topScore: 0,
    selected: [],
    message: "Click an image to begin!"
  }

  handleReset = () => {
    this.setState({
      currentScore: 0,
      selected: [],
      message: "Click an image to begin!"
    })
  }

  handleShuffle = () => {
    let shuffledCards = shuffleCards(lists);
    this.setState({ lists: shuffledCards });
  };

  handleScoreboard = () => {
    let newScore = this.state.currentScore + 1;
    this.setState({ currentScore: newScore });
    if (newScore >= this.state.topScore) {
      this.setState({ topScore: newScore });
    }
    else if (newScore === 12 ) {
      this.setState({ message: "You win!"})
    }
    this.handleShuffle();

  };

  handleClick = (id) => {
    if(this.state.selected.indexOf(id) === -1) {
      this.handleScoreboard();
      this.setState({
        message: "You guessed correctly!", 
        selected: this.state.selected.concat(id) 
      });
    } else {
      this.setState({ message: "You guessed incorrectly!"});
      this.handleReset();
    }
  }

  getPictures = () => {
    return this.state.lists.map( (item) => {
      return (
        <ImageCard 
          key={item.id}
          id={item.id}
          image={item.image}
          handleSelected={this.handleSelected}
          handleScoreboard={this.handleScoreboard}
          handleShuffle={this.handleShuffle}
          handleReset={this.handleReset}
          handleClick={this.handleClick}
        />
      );
    });
  };

  render() {
    return(
      <div>
        <Header
        currentScore={this.state.currentScore}
        topScore={this.state.topScore}
        message={this.state.message} 
        />
        <Jumbotron />
        <div className="container text-center">
          {this.getPictures()}
        </div>
        <Footer />
      </div>
      
    );
  }
}

export default App;
